<template>
	<div id="activity-detail">
		<public-header title="活动详情"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell title="活动图标" class="no-fix flex-lr">
					<div slot="right-icon" class="flex goods-thumb-wrap">
						<img class="goods-thumb " :src="bargain.thumb_" alt="" v-if="bargain.thumb_">
						<img class="goods-thumb " src="static/img/add_pic.png" alt="" v-else>
						<input class="weui-uploader__input" type="file" multiple="multiple" accept="image/*" @change="onUploadImage($event)">
					</div>
				</van-cell>
				<van-field v-model="bargain.title" label="活动主题" placeholder="请输入活动主题" input-align="right" />
				<van-field v-model="bargain.content" label="活动描述" placeholder="请输入活动描述" input-align="right" />
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="开始日期">
					<div class="flex" slot="right-icon" @click="onTogglePopup('starttime')">
						<span class="c-gray">{{util.formatDate(bargain.starttime)}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell title="结束日期">
					<div class="flex" slot="right-icon" @click="onTogglePopup('endtime')">
						<span class="c-gray">{{util.formatDate(bargain.endtime)}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">
						抢购时段<span class="font-12">（开始）</span>
					</div>
					<div class="flex" slot="right-icon" @click="onTogglePopup('starthour')">
						<span class="c-gray">{{bargain.starthour}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">
						抢购时段<span class="font-12">（结束）</span>
					</div>
					<div class="flex" slot="right-icon" @click="onTogglePopup('endhour')">
						<span class="c-gray">{{bargain.endhour}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="与其他优惠同享">
					<iswitch :value="bargain.use_limit" text-open="同享" text-close="互斥" condition-open="1" condition-close="2" slot="right-icon" @change="jsToggleSwitch" keys="bargain.use_limit"></iswitch>
				</van-cell>
				<van-cell title="每个用户每天最多可购买">
					<div class="input-unit" slot="right-icon">
						<input type="text" v-model="bargain.order_limit">
						<span>单</span>
					</div>
				</van-cell>
				<van-cell title="每单可购买特价商品">
					<div class="input-unit" slot="right-icon">
						<input type="text"  v-model="bargain.goods_limit">
						<span>种</span>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="参与商品">
					<div slot="title">
						参与商品<span class="font-12">（不支持多规格产品）</span>
					</div>
					<router-link :to="util.getUrl({path: 'pages/activity/bargainGoods', query: {bargain_id: id}})" class="flex" slot="right-icon">
						<span class="c-gray" v-if="goodsNum > 0">已选择{{goodsNum}}个商品</span>
						<span class="c-gray" v-else>选择商品</span>
						<van-icon name="arrow"></van-icon>
					</router-link>
				</van-cell>
			</van-cell-group>
			<div class="padding-15">
				<van-button size="normal" block class="bg-info font-16" @click="onSubmit">保存</van-button>
			</div>
		</div>
		<!--日期选择-->
		<van-popup v-model="popupStatus.datePicker" position="bottom">
			<div class="popup-content">
				<van-datetime-picker
					v-model="currentDate"
					type="datetime"
					:min-date="minDate"
					@cancel="onTogglePopup(dateType)"
					@confirm="onChooseDate"
				/>
			</div>
		</van-popup>
		<!--时间选择-->
		<van-popup v-model="popupStatus.timePicker" position="bottom">
			<div class="popup-content">
				<van-datetime-picker
					type="time"
					:min-hour="0"
					:max-hour="23"
					@cancel="onTogglePopup(timeType)"
					@confirm="onChooseTime"
				/>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	import {mapState, mapMutations} from 'vuex'
	export default {
		components: {
			publicHeader,
			iswitch
		},
		data() {
			return {
				minDate: new Date(2015, 12, 1),
				currentDate: new Date(),
				currentDate2: new Date(),
				popupStatus: {
					datePicker: false,
					timePicker: false
				},
				dateType: '',
				timeType: '',
				goodsNum: 0,
				id: 0,
				thumb: {},
				bargain: {
					title: '',
					content: '',
					use_limit: 2,
					starttime: new Date(),
					endtime: new Date(),
					starthour: '00:00',
					endhour: '23:59',
					order_limit: "1",
					goods_limit: "1",
					goods: [],
					thumb: '',
					thumb_: '',
				},
				showPreLoading: true
			}
		},
		computed: {
			...mapState([
				'bargainGoods', 'bargainInfo'
			]),
		},
		methods: {
			...mapMutations([
				'setBargainGoods', 'setBargainInfo'
			]),
			onTogglePopup(key) {
				var that = this;
				let popupKey = '';
				if(key == 'starttime' || key == 'endtime') {
					that.dateType = key;
					popupKey = 'datePicker';
				} else if(key == 'starthour' || key == 'endhour') {
					that.timeType = key
					popupKey = 'timePicker';
				}
				this.popupStatus[popupKey] = !this.popupStatus[popupKey];
			},
			onChooseTime(value) {
				this.bargain[this.timeType] = value;
				this.onTogglePopup(this.timeType);
			},
			onChooseDate(value) {
				let timestamp = parseInt(Date.parse(value) / 1000);
				this.bargain[this.dateType] = timestamp;
				this.onTogglePopup(this.dateType);
			},
			onLoad() {
				var that = this;
				let query = that.util.parseQuery(that.$route.query);
				if(query.id > 0) {
					that.id = query.id;
				}
				getData({
					vue: that,
					url: 'manage/activity/bargain/post',
					data: {
						id: that.id
					},
					autoAssign: true,
					variable: 'bargain',
					success: (res) => {
						let bargain = res.bargain;
						if(that.util.length(that.bargainInfo) > 0) {
							bargain = that.bargainInfo;
						}
						that.bargain = that.util.extend(that.bargain, bargain);
						that.goodsNum = that.bargain.goods.length;
						let num = that.util.length(that.bargainGoods);
						if(num > 0) {
							that.goodsNum = num;
						}
					}
				})
			},
			onUploadImage(e) {
				var that = this;
				that.util.image({
					obj: e,
					success: function(obj, result) {
						if(result.url && result.filename) {
							that.bargain.thumb = result.filename;
							that.bargain.thumb_ = result.url;
						}
					},
					options: {channel: 'h5'}
				});
			},
			onSubmit() {
				var that = this;
				let bargainGoods = that.bargain.goods;
				if(that.util.length(that.bargainGoods) > 0) {
					bargainGoods = that.bargainGoods;
				}
				if(that.util.length(bargainGoods) < 1) {
					that.util.$toast('请选择参与活动的商品')
					return;
				}
				setData({
					vue: that,
					url: 'manage/activity/bargain/post',
					data: {
						bargain: that.bargain,
						bargainGoods: bargainGoods,
						id: that.id,
					},
				})
			},
			jsToggleSwitch(res) {
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			}
		},
		watch: {
			bargain: {
				handler(newValue, oldValue) {
					this.setBargainInfo(this.bargain);
				},
				deep: true
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#activity-detail .iswitch{
		height: 24px;
	}
	#activity-detail .goods-thumb-wrap{
		position: relative;
	}
	#activity-detail .weui-uploader__input{
		display: block;
		width: 60px;
		height: 60px;
		opacity: 0;
		position: absolute;
		top: 0px;
		left: 30px;
	}
	#activity-detail .goods-thumb{
		width: 60px;
		height: 60px;
		border-radius: 3px;
		margin-left: 30px;
	}
</style>


